
div#wrapper {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
h1 {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}



































































































































































div#container {
  position: relative;
  max-width: 860px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
div#container h4 {
  margin: 0;
  margin-top: 40px;
}
div#container input, div#container label {
  display: block;
  text-align: left;
}
div#container label {
  margin-top: 5px;
}
div#container input {
  margin-bottom: 10px;
  padding: 3px 0;
}
button#multiline-on {
  float: right;
}
div#container #multiline-container {
  display: none;
}
div#container #multiline-container.enabled {
  display: block;
}
#school-name-field .error-message {
  display: none;
}
#school-name-field.error .error-message {
  display: inline;
  color: red;
}
#school-name-field.error {
  background: #fee;
}
#actions {
  margin: 20px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
#actions .btn {
  text-decoration: none;
  color: #2c3e50;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 2px;
}
#actions .btn:hover {
  color: white;
  background: #006680;
  -moz-box-shadow:    1px 1px 1px 1px #ddd;
  -webkit-box-shadow: 1px 1px 1px 1px #ddd;
  box-shadow:         1px 1px 1px 1px #ddd;
}
.cnv-logo {
  border: 1px solid #eee;
  margin: 0;
  margin-top: 5px;
  -moz-box-shadow:    3px 3px 3px 3px #ddd;
  -webkit-box-shadow: 3px 3px 3px 3px #ddd;
  box-shadow:         3px 3px 3px 3px #ddd;
}
.image-holder {
  display: none;
}
hr {
  margin-top: 50px;
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}
.two-columns {
  width: 100%
}
.two-columns div {
  width: 420px;
}
.two-columns div div {
  display: block
}
#default-logos {
  float: left;
}
#inverse-logos {
  float: right;
}


